html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

button {
  border: none;
}

:root {
  --primaryColor: #fff;
  --navColor1: #edb703;
  --navColor2: #530ea2;
  --navColor3: #ffda5f;
  --navColor4: #2119a7;
  --navColor5: red;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #000;
}

/* --- Navigation Menu ------------------------------ */

// ? Overlay

.overlay {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  transform: translateX(-100vw);
}

//? hide and restore navigation

.overlay-slide-on-screen {
  transition: all 0.4s ease-in-out;
  transform: translateX(0);
}

.overlay-slide-off-screen {
  transition: all 0.8s ease-in-out;
  transform: translateX(-100vw);
}

nav {
  ul {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    list-style-type: none;
    height: 100vh;

    li {
      min-height: 20%;
      overflow: hidden;

      a {
        position: relative;
        top: 45%;
        color: white;
        text-transform: uppercase;
        letter-spacing: 4px;
        text-decoration: none;
        display: block;
        text-align: center;

        &:hover {
          transform: scale(1.2);
        }

        &::before {
          content: "";
          width: 25vw;
          height: 13px;
          background-color: white;
          position: absolute;
          top: 47.5%;
          left: 0;
          opacity: 0;

          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }

  li:nth-of-type(1) {
    background-color: var(--navColor1);
  }
  li:nth-of-type(2) {
    background-color: var(--navColor2);
  }
  li:nth-of-type(3) {
    background-color: var(--navColor3);
  }
  li:nth-of-type(4) {
    background-color: var(--navColor4);
  }
  li:nth-of-type(5) {
    background-color: var(--navColor5);
  }
}

// ? slide in animation for each nav item

.slide-in-1 {
  animation: slide-in 0.4s linear 0.2s both;
}
.slide-in-2 {
  animation: slide-in 0.4s linear 0.4s both;
}
.slide-in-3 {
  animation: slide-in 0.4s linear 0.6s both;
}
.slide-in-4 {
  animation: slide-in 0.4s linear 0.8s both;
}
.slide-in-5 {
  animation: slide-in 0.4s linear 1s both;
}

@keyframes slide-in {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

.slide-out-1 {
  animation: slide-out 0.3s linear 0.5s both;
}
.slide-out-2 {
  animation: slide-out 0.3s linear 0.4s both;
}
.slide-out-3 {
  animation: slide-out 0.3s linear 0.3s both;
}
.slide-out-4 {
  animation: slide-out 0.3s linear 0.2s both;
}
.slide-out-5 {
  animation: slide-out 0.3s linear 0.1s both;
}

@keyframes slide-out {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

// ? nav menu items

/* --- Menu Bars ------------------------------------ */

.menu-bars {
  position: fixed;
  top: 1rem;
  right: 2rem;
  z-index: 10;
  display: inline;
  cursor: pointer;
}

.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 2px;
  background-color: #fff;
  margin: 8px 0;
  transition: 0.4s;
}

/* Rotate first bar */
.change .bar1 {
  transform: rotate(-45deg) translate(-7px, 8px);
}

/* Fade out the second bar */
.change .bar2 {
  opacity: 0;
}

/* Rotate last bar */
.change .bar3 {
  transform: rotate(45deg) translate(-6px, -8px);
}

/* --- Sections ------------------------------------ */
.site-wrapper {
  overflow-x: hidden;
}
.main {
  a {
    text-decoration: none;
    color: #000;

    font-size: 2rem;

    &:hover {
      color: white;
    }
  }

  p {
    display: block;
    margin-top: 0.5rem;
    text-align: right;

    font-size: 1.5rem;
  }

  h1 {
    font-size: 5rem;
  }
}
section {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--primaryColor);
}

section#home {
  background-image: url("/super-navigation/img/bg.jpeg");
  background-size: cover;
  background-position: center center;
}

section#home .link {
  position: absolute;
  z-index: 2;
  top: 1.5rem;
  left: 1.5rem;
  text-decoration: none;
  font-size: 0.8rem;
  padding-bottom: 5px;
  color: var(--primaryColor);
  border-bottom: 1px solid var(--primaryColor);
}

section#about {
  background-color: var(--navColor2);
}

section#skills {
  background-color: var(--navColor3);
}

section#projects {
  background-color: var(--navColor4);
}

section#contact {
  background-color: var(--navColor5);
}

/* Media Query: Large Smartphone (Vertical) */
@media (max-width: 600px) {
  section#home a {
    top: 1rem;
    left: 1rem;
  }

  .menu-bars {
    top: 0.5rem;
    right: 1rem;
  }

  .main {
    display: flex;
    flex-direction: column;
    padding: 0 2rem;
    a {
      font-size: 1.5rem;
      color: white;

      &:hover {
        color: black;
      }
    }

    p {
      font-size: 1rem;
      color: black;
    }

    h1 {
      font-size: 3rem;
    }
  }

  section#home {
    background-size: cover;
    background-position: left center;
  }

  nav ul li a {
    font-size: 3rem;
  }
}
